import React, { Component } from 'react';
import { Row, Col } from 'antd';
import { getAction } from '../../../axios'
// import jsonData from '../../../data/school/学院视角--基本情况--学生情况.json';

// const dataInfo = jsonData.RECORDS[0];

export default class BasicSituationStudentSituation extends Component {
  state={
    dataInfo: {}
  }
  componentDidMount() {
    this.getData();
  }
  getData() {
    getAction("/bigdata/school/basic/queryStudent").then((res) => {
      if (res.success) {
        this.setState({dataInfo: res.obj})
      }
    })
  }
  render() {
    let { dataInfo } = this.state;
    return (
      <Row type="flex" align="middle" style={{ height: "100%", textAlign: "center", color: "#fff" }}>
        <Col span={8}>2018年</Col>
        <Col span={8}>
          <div>学生总人数</div>
          {/* <div>本科生人数</div> */}
          <div>专科生人数</div>
          <div>士官生人数</div>
        </Col>
        <Col span={8} style={{ color: "#FADB14" }}>
          <div>{dataInfo.collegeStudentNum}人</div>
          {/* <div>{dataInfo.undergraduateStudentNum}人</div> */}
          <div>{dataInfo.juniorStudentNum}人</div>
          <div>{dataInfo.cadetStudentNum}人</div>
        </Col>
      </Row>
    )
  }
}
