import React, { PureComponent } from 'react';
import { Row, Col } from 'antd';
import echarts from 'echarts';
import { getAction } from '../../../axios'
// import ageJson from '../../../data/school/学院视角--教职工人员情况--年龄.json';
// import xueliJson from '../../../data/school/学院视角--教职工人员情况--学历.json';
// import xueyuanJson from '../../../data/school/学院视角--教职工人员情况--学源.json';
// import zhichengJson from '../../../data/school/学院视角--教职工人员情况--职称.json';

class ItemComp extends PureComponent {
    componentWillReceiveProps(nextProps){
        if(JSON.stringify(this.props) !== JSON.stringify(nextProps)){
            let data = nextProps.data || [];
            // let total = data.map(item=>item.value).reduce((a,b)=>a+b);
            let legendData = data.map(item=>item.name)
            this.setChart(legendData, data);
        }
    }
    componentDidMount() {
        let data = this.props.data || [];
        // let total = data.map(item=>item.value).reduce((a,b)=>a+b);
        let legendData = data.map(item=>item.name)
        this.setChart(legendData, data);
    }
    setChart(legendData, data){
        let myChart = echarts.init(document.getElementById(this.props.id));
        let option = {
            title: {
                text: this.props.name,
                left:'22%',
                top:'center',
                textStyle:{
                    color:'#00CFFF',
                    fontSize:'22',
                    align:'center'
                }
            },
            color: ["#00CFFF","#04FFC7","#65D62D","#F8E71C","#FF2C2C","#1890FF","#BD10E0","#00E8FF"],
            legend: {
                orient: 'vertical',
                x: '60%',
                y: 'center',
                icon: 'circle',
                itemWidth: 5,
                itemHeight: 5,
                itemGap: 13,
                textStyle: {
                    color: "#00CFFF",
                },
                data:legendData,
                formatter: (name) => {
                    let cur = data.filter(item=>item.name===name)[0];
                    return name + ":" + cur.value;
                }
            },
            series: [
                {
                    name:"",
                    type:'pie',
                    center: ['30%','50%'],
                    radius: ['50%', '70%'],
                    avoidLabelOverlap: false,
                    label: {
                        show: false,
                        position: 'center'
                    },
                    labelLine: {
                        normal: {
                            show: false
                        }
                    },
                    data:data
                }
            ]
        };

        myChart.setOption(option);
    }
    render() {
        return (
            <div id={this.props.id} style={{width:"100%",height:"100%"}}></div>
        )
    }
}

export default class StaffSituation extends PureComponent {
    state = {
        ageData: [],
        xueliData: [],
        xueyuanData: [],
        zhichengData: []
    }
    componentDidMount(){
        this.getData();
    }
    getData(){
        getAction("/bigdata/school/teacher/queryTeacherInfo").then((res) => {
            if (res.success) {
                if(res.obj['职称']){
                    let zhichengData = res.obj['职称'].map(item=>{
                        return {
                            name: item.vocationName,
                            value: item.number,
                        }
                    });
                    this.setState({zhichengData});
                }
                if(res.obj['年龄']){
                    let ageData = res.obj['年龄'].map(item=>{
                        return {
                            name: item.ageType,
                            value: item.ageNum,
                        }
                    });
                    this.setState({ageData});
                }
                if(res.obj['学源']){
                    let xueyuanData = res.obj['学源'].map(item=>{
                        return {
                            name: item.sourceType,
                            value: item.sourceNum,
                        }
                    });
                    this.setState({xueyuanData});
                }
                if(res.obj['学历']){
                    let xueliData = res.obj['学历'].map(item=>{
                        return {
                            name: item.education,
                            value: item.number,
                        }
                    });
                    this.setState({xueliData});
                }
            }
        })
    }
  render() {
        let datas = [
            { 
                type: "职称",
                data: this.state.zhichengData
            },
            { 
                type: "年龄",
                data: this.state.ageData
            },
            { 
                type: "学源",
                data: this.state.xueyuanData
            },
            { 
                type: "学历",
                data: this.state.xueliData
            },
        ]
        return (
            <Row style={{height:"100%"}}>
                {datas.map((item,i)=>(
                    <Col span={6} style={{height:"100%"}} key={i}>
                        <ItemComp name={item.type} id={"ZGDStaffSituationChart"+i} data={item.data}/>
                    </Col>
                ))}
            </Row>
        )
  }
}
