import React, { Component } from 'react';
import { Row, Col } from 'antd';
import { getAction } from '../../../axios'
// import jsonData from '../../../data/school/学院视角--基本情况--图书馆藏.json';

// const dataInfo = jsonData.RECORDS[0];

export default class BasicInformationLibraryCollection extends Component {
  state={
    dataInfo: {}
  }
  componentDidMount() {
    this.getData();
  }
  getData() {
    getAction("/bigdata/school/basic/queryLibrary").then((res) => {
      if (res.success) {
        this.setState({dataInfo: res.obj})
      }
    })
  }
  render() {
    let { dataInfo } = this.state;
    return (
      <Row type="flex" align="middle" style={{height:"100%",textAlign:"center",color:"#fff"}}>
        <Col span={8}>2018年</Col>
        <Col span={8}>
            <div>图书总数</div>
            {/* <div>纸质书</div>
            <div>数字资源</div> */}
        </Col>
        <Col span={8} style={{color:"#FADB14"}}>
            <div>{dataInfo.bookNum}</div>
            {/* <div>{dataInfo.paperBookNum}</div>
            <div>{dataInfo.ebookNum}</div> */}
        </Col>
      </Row>
    )
  }
}
